.religious-carousel {
    width: 900px;
    box-shadow: 0px 4px 17px 10px rgba(0, 0, 0, 0.25);
}

.top-progrm {
    height: 100%;
}

.head-slide {
    height: 100px;
    padding-top: 30px;
}

.block-progm {

    width: 100%;
    height: 500px;
}

.img-padre {
    width: 130px !important;
    height: 130px !important;
    border-radius: 400px;
    border: 5px solid #BF760A !important;

}

.img-padre-terca {
    width: 110px !important;
    height: 110px !important;
    border-radius: 400px;
    border: 5px solid #BF760A !important;
}

.pgrm-conteiner {
    display: flex;
    align-items: center;
    height: 60%;
    justify-content: space-around;


}

.text-block-progrm {
    text-align: left;
    width: 450px;
}

.progrm-terca {
    text-align: left;
    width: 500px;
    padding-left: 30px;
}

.program2 {
    display: flex;
    flex-direction: column;
    height: 500px;

}

.program2 h2 {
    margin: 2px;
}

.parte-de-cima {
    display: flex;
    align-items: center;
    height: 30%;
    justify-content: space-around;
    font-size: small;
}

.parte-baixo {
    display: flex;
    align-items: center;
    height: 40%;
    justify-content: space-around;
    font-size: small;
}

.program-dom-dulcenio {
    text-align: left;
}

@media (max-width: 650px) {
    .religious-container #Religious {
        height: fit-content;
        font-size: 90%;

    }

    .programacao {
        width: 90%;
        height: fit-content;
    }

    .religious-carousel {
        width: 100%;
        height: 40vh;
    }

    .pgrm-conteiner {
        font-size: 70%;
        width: 95%;
        height: 20vh;
    }

    .top-progrm {
        height: fit-content;
        display: flex;
        flex-direction: column;
    }

    .text-block-progrm {
        width: 60%;
        padding-left: 4%;
    }
    .head-slide{
        font-size: 1rem;
    }

}