.invite-texto {
    width: 90%;
}

.invite-conteiner {

    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    row-gap: 30px;
}

.btn-programacao {

    width: 300px;
    height: 50px;
    border-radius: 25px;
    background-color: #BF760A;
    color: white;
    font-size: large;
    font-family: 'Abhaya Libre', serif;
    font-weight: 600;
    border: none;
}

.btn-programacao:hover {

    background-color: #8d5809;
}

.btn-programacao:active {
    background-color: #653f05;
}


@media (max-width: 650px) {
    .invite-h1 {
        font-size: 1rem;
        word-wrap: break-word;
    }

    .invite-texto {
        width: 85%;
    }

    .btn-programacao {
        width: 90%;
        height: max-content;
        font-size: 90%;
        padding: 3%;
    }
    .videosj{
        width: 90% !important;
        height: 20vh !important;
    }
    .invite-conteiner{
        width: 90%;
    }
}